<template>
    <div class="accounting-wrap" v-if="orders.length > 0">
        <h2>会計前画面</h2>
        <table>
            <tr>
                <th>商品名</th>
                <th>注文数</th>
                <th>合計金額</th>
            </tr>
            <tr v-for="order in orders" :key="order.name">
                <td>{{ order.name }}</td>
                <td>{{ order.total_amount }}</td>
                <td>{{ order.total_price }}</td>
            </tr>
        </table>
        
        <button type="button" @click="finishOrder">会計を行う</button>
    </div>
    <div class="accounting-wrap" v-else-if="getStatus() === 'お会計待ち'">
        <section>
            <h2>お会計待ちの状態です。</h2>
            <p>レジでお会計を行なってください。</p>
        </section>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name: 'UserAccounting',
    data() {
        return {
            orders: []
        }
    },

    methods: {
        finishOrder() {
            const table = sessionStorage.getItem('table');
            if(table) {
                const sendData = {
                    table: table
                };
                Ajax('/api/order/accounting', 'POST', sendData);
            }
            this.orders = [];
            window.location.reload();
        },

        getStatus() {
            const table = sessionStorage.getItem('table');
            let stat = '';
            if(table) {
                Ajax(`/api/table/status/${table}`)
                .then(res => res.json())
                .then(dat => stat = dat);
            }
            return stat;
        }
    }
}
</script>
